.footer {
  &__container {
    display: grid;
    padding-top: 60px;
    padding-bottom: 60px;
    border-top: 1px solid #dedfe1;
    grid-template-columns: auto 1fr;
    row-gap: 70px;
    column-gap: 20px;

    @media (max-width: 767.98px) {
      padding-top: 30px;
      padding-bottom: 30px;
      grid-template-columns: 1fr;
      justify-items: center;
      row-gap: 30px;
    }
  }

  &__logo {
    font-weight: 800;
    font-size: 23px;
    line-height: 31px;
    color: inherit;
  }

  &__menu {
    display: flex;
    justify-content: flex-end;
  }

  &__copy {
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    text-align: center;

    @media (min-width: 768px) {
      grid-column: span 2;
    }
  }
}

.menu-footer {
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 45px;
    row-gap: 15px;
  }

  &__link {
    font-weight: 700;
    color: inherit;
    font-size: 13px;
    line-height: 17px;

    &:hover {
      text-decoration: underline;
    }
  }
}
